*{
    margin: 0px;
    padding: 0px;
}
body{
    background-color: white;
}
label{
    font-style: italic;
    color: grey;
    font-size: 1.7vw;
}
a, li{
    list-style: none;
    text-decoration: none;
}
button:hover, input:hover{
    cursor: pointer;
}
h3{
    font-weight: 200;
    font-family: cursive;
    font-size: 4vw;
    letter-spacing: 0.3vw;
}
.green{
    color: rgb(112, 152, 50);
    font-weight: 800;
}
.red{
    color:red;
}
.backBtn{  
    position: sticky;
    top:2vw;
    left:  calc(100% - 5vw);
    width: 5vw;
    height: 5vw;
    background-image: url('./images/arrow_circle_up_icon_246075.svg');
    background-size: contain;
    background-repeat: no-repeat;
    a{
        display: inline-block;
        width: inherit;
        height: inherit;
    }
}
.header{
    display: flex;
    justify-content: center;
    margin: 1vw 0 0.5vw;
}
.check{
    text-decoration: line-through;
}